/* normalize.css v8.0.1 */
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dottedButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.5rem;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1 {
  font-size: 2.6rem;
}

h2 {
  font-size: 2.3rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.7rem;
}

p {
  font-size: 1.5rem;
  color: #000000;
  font-weight: 400;
  line-height: 1.5;
}

.text-center {
  text-align: center;
}

.dev {
  height: 50vh;
  display: flex;
  background-color: #10439F;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.dev__contenido {
  width: 95%;
  max-width: 40%;
}
.dev__logo {
  font-size: 3rem;
  font-weight: 700;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}
.dev__tag {
  color: #FFFFFF;
}
.dev__nombre {
  margin: 0;
  color: #FFFFFF;
}
.dev__descripcion {
  color: #FFFFFF;
}

.header {
  min-height: 60rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, black, rgba(0, 0, 0, 0.5)), image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 40rem, 40rem, cover, cover;
  background-position: top left, bottom right, center center, center center;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__logo {
  width: 10rem;
}
.header__barra {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  padding: 0.5rem 6rem;
  border-bottom-left-radius: 5rem;
  border-bottom-right-radius: 5rem;
}
.header__navegacion {
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    justify-content: flex-end;
  }
}

.footer {
  background-color: #071c42;
  padding: 3rem 0;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 1024px) {
  .footer__grid {
    flex-direction: row;
  }
}
.footer__logo {
  width: 20rem;
}
.footer__copy {
  text-align: center;
  color: #b3b3b3;
  font-weight: 700;
}
.footer__copy--regular {
  font-weight: 400;
}

.menu_redes {
  display: flex;
  gap: 2rem;
}
.menu_redes__enlace::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu_redes__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.menu_redes__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.menu_redes__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.menu_redes__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.menu_redes__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.menu_redes__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.menu_redes__ocultar {
  display: none;
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: row;
  background-color: #ececec;
  position: relative;
}
.dashboard__grid {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.dashboard__header {
  padding: 1.5rem 2rem;
  background-color: #071c42;
}
.dashboard__contenido {
  padding: 1rem 2rem;
  width: 100%;
  overflow-y: scroll;
}

.header_admin {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.header_admin__menu {
  color: #FFFFFF;
  font-size: 2rem;
}
.header_admin__menu:hover {
  cursor: pointer;
}
.header_admin__icono {
  color: #FFFFFF;
}
.header_admin__perfil {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.header_admin__informacion {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  color: #FFFFFF;
}
.header_admin__informacion:hover {
  cursor: pointer;
}
.header_admin__informacion::after {
  content: "";
  width: 0.6rem;
  height: 0.6rem;
  border-bottom: 2px solid #FFFFFF;
  border-right: 2px solid #FFFFFF;
  rotate: -45grad;
}
.header_admin__icono {
  border: 1px solid #FFFFFF;
  border-radius: 50%;
  padding: 0.4rem;
}
.header_admin__usuario {
  font-weight: 500;
}
.header_admin__config {
  padding: 0;
  margin: 0;
  list-style: none;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  position: absolute;
  background-color: #c4c4c4;
  padding: 2rem;
  border-radius: 1rem;
  right: 0;
  z-index: 1;
  margin-top: 3.5rem;
}
.header_admin__item {
  margin-bottom: 1rem;
}
.header_admin__item:last-of-type {
  margin-bottom: 0;
}
.header_admin__enlace {
  color: #000000;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.header_admin__icon {
  width: 2.5rem;
}
.header_admin__nombre {
  font-weight: 400;
}

.sidebar {
  background-color: #092659;
  visibility: hidden;
  opacity: 0;
  width: 0;
  transition: visibility 0.5s linear, opacity 0.5s linear, width 0.5s linear;
}
@media (min-width: 1024px) {
  .sidebar {
    width: 25rem;
    visibility: visible;
    opacity: 1;
  }
}
.sidebar--open {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  bottom: 0;
  visibility: visible;
  opacity: 1;
  width: 25rem;
}
@media (min-width: 1024px) {
  .sidebar--open {
    position: none;
    opacity: 0;
    width: 0;
    z-index: 0;
  }
}
@media (min-width: 1024px) {
  .sidebar__cerrar-menuadmin {
    visibility: hidden;
    opacity: 0;
  }
}
.sidebar--close {
  display: none;
}
.sidebar__logo {
  display: flex;
  justify-content: space-between;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-weight: 700;
  align-items: center;
}
.sidebar__cerrar-menuadmin {
  font-size: 3rem;
}
.sidebar__cerrar-menuadmin:hover {
  cursor: pointer;
}

.menu_admin {
  padding: 0;
  margin: 0;
  list-style: none;
  margin: 1rem;
}
.menu_admin__item {
  margin-bottom: 2rem;
}
.menu_admin__enlace {
  color: #999999;
  font-weight: 500;
  white-space: nowrap;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 1rem;
  gap: 1rem;
}
.menu_admin__enlace:hover {
  cursor: pointer;
  color: #FFFFFF;
}
.menu_admin__icono::after {
  content: "";
  width: 0.6rem;
  height: 0.6rem;
  border-bottom: 2px solid #FFFFFF;
  border-right: 2px solid #FFFFFF;
  rotate: -45grad;
  margin-right: 0.2rem;
}
.menu_admin__nombre {
  flex-grow: 1;
}
.menu_admin__submenu {
  padding: 0;
  margin: 0;
  list-style: none;
  padding-left: 2rem;
  font-size: 1.2rem;
  display: none;
}
.menu_admin__open .menu_admin__submenu {
  display: block;
}
.menu_admin__open .menu_admin__icono::after {
  rotate: 50grad;
}